body {
  margin: 0;
  box-sizing: border-box;
  color: #fff;
  font-family: 'Montserrat', sans-serif;

}

* {
  outline: none;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}

.wrapper {
  overflow: hidden;
  margin-left: auto;
  margin-right: auto;
  background: linear-gradient(246.17deg, #3D3D3D 0.5%, #0B0B0B 100%);
}

p {
  font-size: 18px;
  line-height: 1.3;
}

p.text {
  font-weight: 300;
  font-size: 18px;
  line-height: 1.3;
  text-align: center;
}

.unit {

}

.wr {
  position: relative;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}

.nav {
  display: none;

}

.nav_active {
  position: fixed;
  width: 100%;
  z-index: 5;
  top: 0;
  left: 0;
  background: rgba(14, 14, 14, 0.95);
  display: flex;
  flex-flow: column;
  height: 100%;
  justify-content: space-between;

  .title-nav {
    margin-top: 43px;
    text-align: center;
    font-weight: 900;
    font-size: 24px;
    line-height: 1.3;
    color: #FFFFFF;
    margin-bottom: 28px;
  }

  .soc-wr {
    padding-top: 30px;
    margin-bottom: 70px;

    a {
      padding-top: 0;
      padding-bottom: 0;
    }
  }

  a {
    text-decoration: none;
    text-align: center;
    color: #fff;
    font-weight: 500;
    font-size: 18px;
    line-height: 1.3;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-top: 1px;
  }

  .line_top:before {
    top: 1px;
  }
}

.nav-link-wr {
  display: flex;
  flex-flow: column;

}

.burger {
  width: 29px;
  height: 23px;
  overflow: hidden;
  position: fixed;
  z-index: 11;
  left: 15px;
  top: 40px;

  input {
    display: none;
  }
}

.burger label {
  width: 29px;
  height: 23px;
  display: block;
  -webkit-transition: all 0.35s;
  transition: all 0.35s;
  cursor: pointer;
}

.bar {
  width: 100%;
  height: 3px;
  display: block;
  position: absolute;
  left: 0;
  background: #fff;
  -webkit-transition: all 0.35s;
  transition: all 0.35s;
  pointer-events: none;
  border-radius: 1px;
}

.bar-1 {
  top: 4px;
}

.bar-2 {
  top: 11px;
  width: 60%;
}

.bar-3 {
  top: 18px;
}


/* ===------ Burger 1 ------=== */
.burger-1 input:checked ~ label .bar-1 {
  top: 10px;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.burger-1 input:checked ~ label .bar-2 {
  left: -100%;
}

.burger-1 input:checked ~ label .bar-3 {
  top: 10px;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.btn {
  border: none;
  background: linear-gradient(90deg, #0C0C0C 0%, #242424 100%);
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.5);
  border-radius: 30px;
  font-weight: 300;
  font-size: 14px;
  line-height: 1.3;
  text-transform: uppercase;
  color: #FFFFFF;
  padding: 18px;
  text-decoration: none;
  box-sizing: border-box;
  text-align: center;

  &_simple {
    background: linear-gradient(270deg, #F89D16 0%, #E8B531 100%);
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.5);
    border-radius: 30px;
    font-weight: 300;
    font-size: 14px;
    line-height: 1.3;
    text-transform: uppercase;
    color: #242424;
    padding-left: 45px;
    padding-right: 45px;
  }
}

.btn-wr {
  display: flex;
  justify-content: center;
  margin-bottom: 56px;
}

.unit_header {
  position: relative;
  height: 83px;

  &:before {
    position: absolute;
    content: '';
    background-image: url(../img/line_round.png);
    width: 594px;
    height: 594px;
    background-repeat: no-repeat;
    background-position: 72px -20px;
    z-index: 0;

  }
}

.wr_header {
  position: relative;
  z-index: 1;
  padding-top: 26px;
}

.title {
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 1.3;
  color: #fff;

  span {
    color: #F89D16;
  }

}

.unit_first {
  position: relative;

  &:before {
    position: absolute;
    content: '';
    background-image: url(../img/logo.png);
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: right 69%;
    z-index: 0;
  }
}

h1.title {
  font-size: 32px;
  margin-bottom: 25px;
}

h1 + p {
  margin-bottom: 32px;
}

.sl {
  margin-top: 30px;
}

.sl-item {
  border-radius: 20px;
  overflow: hidden;
  position: relative;

  .btn {
    position: absolute;
    bottom: 20px;
    left: calc(50% - 143px);
    width: 286px;
  }

  img {
    width: 100%;
    height: 100%;
  }
}

.slick-dots {
  display: flex;
  justify-content: center;
  list-style-type: none;
  margin-top: 50px;
  margin-bottom: 50px;

  li {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background: #000000;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
    margin-left: 6px;
    margin-right: 6px;
  }

  li.slick-active {
    background: #F89D16;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5)
  }

  button {
    display: none;
  }
}

.arrow-sl-wr {
  width: 100%;
  display: flex;
  justify-content: space-between;
  position: relative;

  .slick-arrow.slick-hidden {
    display: flex;
  }
}

.arrow-sl {
  position: absolute;
  bottom: 36px;
  display: flex;
  align-items: center;
  height: 42px;
  width: 13px;

  img {
    width: auto;
    height: 100%;
  }
}

.arrow-sl_left {
  left: 0;
}

.arrow-sl_right {
  right: 0;
  transform: rotate(180deg);
}

.form {
  background: linear-gradient(90deg, #0C0C0C 0%, #242424 100%);
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.5);
  border-radius: 30px;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 33px;

  .title-form {
    font-weight: 300;
    font-size: 12px;
    margin-bottom: 17px;

    line-height: 1.3;
    text-align: center;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.5);
  }

  .radio-wrapper-product {
    display: flex;
    flex-flow: column;
    height: 80px;
    overflow-y: scroll;
    margin-bottom: 30px;
    scroll-behavior: smooth;

    input {
      width: 0;
      height: 0;
      background: transparent;
      color: transparent;
    }

    span {
      width: 100%;
      display: flex;
      justify-content: center;
      padding-top: 13px;
      padding-bottom: 13px;
    }

    label {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      font-style: normal;
      font-weight: 300;
      font-size: 12px;
      line-height: 1.3;
      text-align: center;
      text-transform: uppercase;
      color: rgba(255, 255, 255, 0.5);

    }

    label input:checked + span {
      font-size: 14px;
      color: #FFFFFF;
      background-image: url(../img/arrow_tb.png);
      background-repeat: no-repeat;
      background-position: right center;
    }
  }

  .radio-wrapper-size {
    display: flex;
    justify-content: center;
    width: 100%;

    label {
      font-weight: 300;
      font-size: 20px;
      line-height: 1.3;
      text-align: center;
      text-transform: uppercase;
      color: #FFFFFF;
      width: 20%;
    }

    input {
      width: 0;
      height: 0;
      background: transparent;
      color: transparent;
    }

    label:focus-within, label input:checked + span {
      color: #F89D16;
    }
  }


  .btn {
    margin-top: 48px;
    background: linear-gradient(270deg, #0C0C0C 0%, #242424 100%);
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.5);
    border-radius: 30px;
    margin-bottom: -25px;
  }

  .form-input-wr {
    position: relative;
    display: flex;
    flex-flow: column;

    input {
      width: 100%;
      background: transparent;
      border: none;
      font-weight: 300;
      font-size: 14px;
      line-height: 1.3;
      text-align: center;
      text-transform: uppercase;
      color: rgba(255, 255, 255, 0.5);
      margin-top: 28px;
      padding-bottom: 6px;
    }
  }

  .label {
    background-repeat: no-repeat;
    background-position: left 26px;
  }

  .label-user {
    background-image: url(../img/input_iser.svg);

  }

  .label-phone {
    background-image: url(../img/input_phone.svg);

  }
}

.line {
  width: 100%;
  position: relative;

  &:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-image: url('../img/Line.svg');
    background-repeat: no-repeat;
    background-size: cover;
  }

  &_top {
    &:before {
      top: 0;
    }
  }
}

.title_footer {
  font-size: 24px;
  text-align: center;
  margin-bottom: 0;

}

.soc-wr {
  display: flex;
  justify-content: space-around;
  margin-top: 80px;
  margin-bottom: 40px;

  .soc-link {
    width: 43px;
    height: 43px;

    svg path {
      fill: #fff;
    }

    &:hover {
      svg path {
        fill: #F89D16;
      }
    }
  }
}

.copywrite {
  font-weight: normal;
  font-size: 12px;
  line-height: 1.3;
  text-align: center;
  color: #FFFFFF;
  padding-bottom: 13px;
}

.unit_header {

}

.unit_form {
  margin-bottom: 50px;
}

.unit_sl-2 {
  position: relative;

  .logo {
    text-align: right;
    padding-top: 55px;
    margin-bottom: 23px;

  }

  &:before {
    position: absolute;
    content: '';
    background-image: url(../img/line_round.png);
    width: 594px;
    height: 594px;
    background-repeat: no-repeat;
    background-position: -242px 10px;
    z-index: 0
  }
}

.unit_five {
  margin-top: 50px;
  background-image: url(../img/bg_five.png);
  background-repeat: no-repeat;
  background-position: top center;
}

.img_brend {
  width: 100%;
  margin-top: 50px;
  border-radius: 20px;
  margin-bottom: 38px;
}

.unit_step {
  .logo {
    margin-top: 60px;
    text-align: right;
    margin-bottom: -30px;
  }
}

.custom-btn {
  overflow:hidden;
  position: relative;

}

.custom-btn:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  z-index: 1;
  transition: ease 0.3s;
}

.custom-btn:after {
  content: "";
  display: block;
  width: 30px;
  height: 300px;
  margin-left: 60px;
  background: #fff;
  position: absolute;
  left: -40px;
  top: -150px;
  z-index: 1;
  transform: rotate(45deg);
  transition: all 0.1s;
  animation-name: slideme;
  animation-duration: 3s;
  animation-delay: 0.05s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
}

@-webkit-keyframes slideme {
  0% {
    left: -30px;
    margin-left: 0px;
  }
  30% {
    left: 110%;
    margin-left: 80px;
  }
  100% {
    left: 110%;
    margin-left: 80px;
  }
}

.step-wr {
  display: flex;
  flex-flow: column;
}

.step-item {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  height: 170px;
  padding-left: 20px;

  &:before {
    content: '';
    background-image: url(../img/step-icon.png);
    position: absolute;
    width: 5px;
    height: 75px;
    background-repeat: no-repeat;
    bottom: -39px;
    left: 57px;

  }

  &:last-child {
    &:before {
      display: none;
    }
  }
}

.step-item-round {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 83px;
  height: 83px;
  background: #242424;
  border-radius: 50%;
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.4);
  margin-right: 24px;
  z-index: 1;
  position: relative;

  &:before {
    position: absolute;
    font-weight: 900;
    font-size: 144px;
    line-height: 1.3;
    color: rgba(255, 255, 255, 0.09);
    bottom: 0;
    left: -20px;
    z-index: -1;
  }
}

.step-item:nth-child(1) .step-item-round {
  &:before {
    content: '1';
  }
}

.step-item:nth-child(2) .step-item-round {
  &:before {
    content: '2';
  }
}

.step-item:nth-child(3) .step-item-round {
  &:before {
    content: '3';
  }
}

.step-item:nth-child(4) .step-item-round {
  &:before {
    content: '4';
  }
}

.step-item-text {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 1.3;
  color: #FFFFFF;
  width: calc(100% - 83px - 24px);
}

.form_popup {
  .logo {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 25px;
  }
}

.fancybox-content {
  padding: 0;
  padding-bottom: 50px;
  width: 96%;
  margin-left: auto;
  margin-right: auto;
  background: transparent;
}

.wr_thanks {
  height: 100vh;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;

  .soc-wr {
    margin-top: 40px;
    padding-top: 32px;
  }
}

